<template>
    <div class="wrapper">
        <div class="container">
            <p class="title">Your account has been deleted.</p>
            <button
                class="button pink full-width"
                @click.click="goToRegistration()"
            >
                Make A New Account
            </button>
            <button class="cancel" @click.click="closeModal()">Dismiss</button>
        </div>
    </div>
</template>

<script>
import { EventBus } from "../../../utilities/eventBus.js";
import { mapGetters } from "vuex";

import $ from "webpack-zepto";

export default {
    data() {
        return {
            showModal: false,
        };
    },
    methods: {
        goToRegistration() {
            this.$store.commit("auth/logOut", { force: true });
            this.$router.push({ path: "/signup/1" });
        },
        closeModal() {
            // Loading intro page will force logout
            this.$router.push({ path: "/intro" });
        },
    },
};
</script>
